@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR);
body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  box-sizing: inherit; }

a {
  text-decoration: inherit;
  color: inherit; }

.App {
  width: 700px;
  margin: 0 auto; }

.header {
  padding: 20px 0; }
  .header::after {
    display: block;
    content: '';
    clear: both; }
  .header > .left {
    float: left; }
  .header > .right {
    float: right; }
    .header > .right .join {
      margin-left: 1rem; }

.loginWrap {
  padding: 50px; }
  .loginWrap .title {
    text-align: center;
    font-size: 2.5rem; }
  .loginWrap .loginForm .loginBtn {
    margin-top: 1rem; }
  .loginWrap .joinBtn {
    padding-top: 1rem;
    text-align: right; }

.joinWrap {
  padding: 50px; }
  .joinWrap .title {
    text-align: center;
    font-size: 2.5rem; }
  .joinWrap .joinForm .textField > div.error:after {
    border-bottom-color: red; }
  .joinWrap .joinForm .joinBtn {
    margin-top: 1rem; }

.lists .table th,
.lists .table td {
  padding: 4px 24px; }

.lists .table .row1 {
  width: 10%;
  padding: 4px 15px; }

.lists .table .row2 {
  width: 50%; }
  .lists .table .row2 a {
    color: rgba(0, 0, 0, 0.87); }

.lists .table .row3 {
  width: 20%; }

.lists .table .row4 {
  width: 20%; }

.postWrap {
  padding: 30px; }
  .postWrap .title {
    margin-bottom: 1rem; }
  .postWrap .sub {
    margin-bottom: 1rem;
    display: flex; }
    .postWrap .sub > div {
      flex: 1 1; }
    .postWrap .sub .CreatedAt {
      text-align: right; }
  .postWrap .contents {
    margin-bottom: 1rem; }

